import React, { useEffect, useState } from 'react';
import cc from 'classcat';
import { IconType } from './Icon.types';
import './Icon.css';

export interface IconProps {
  icon: IconType;
  size?: 'sm' | 'md' | 'lg';
}

const Icon = ({ icon, size = 'md', ...restProps }: IconProps) => {
  const [LazyIcon, setLazyIcon] = useState(null);

  useEffect(() => {
    const importIcon = async () => {
      try {
        const { default: importedIcon } = await import(`./icons/${icon}.svg`);
        setLazyIcon(importedIcon);
      } catch (error) {
        console.warn(`Failed to load icon: \`${icon}\``);
      }
    };
    importIcon();
  }, [icon]);

  if (!LazyIcon) {
    return null;
  }

  return <span className={cc(['icon_root', `icon--${size}`])}>{LazyIcon}</span>;
};

export { Icon };
